/**
* Fixe le scroll en arrière plan
*/
:root[data-fr-scrolling] body {
  overflow: hidden;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
}

.fr-sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;

  /* added line */
  border: 0;
}

.fr-hr {
  padding: var(--text-spacing);
  margin: 0;
  border: 0;
  display: block;
  background-size: 100% 1px;
  background-repeat: no-repeat;
  background-position: 0 0;
  background-image: linear-gradient(0deg, var(--border-default-grey), var(--border-default-grey));
}

.fr-hr--sm {
  width: 10rem;
  margin-left: auto;
  margin-right: auto;
}

.fr-responsive-img {
  width: 100%;
  height: auto;
}

.fr-responsive-vid {
  position: relative;
  width: 100%;
}

.fr-responsive-vid::before {
  content: "";
  display: block;
  width: 100%;
  padding-bottom: 56.25%;
}

.fr-responsive-vid--4x3::before {
  content: "";
  display: block;
  padding-bottom: 75%;
}

.fr-responsive-vid--1x1::before {
  content: "";
  display: block;
  padding-bottom: 100%;
}

.fr-responsive-vid__player,
.fr-responsive-vid > .fr-consent-placeholder {
  object-fit: cover;
  object-position: 50% 50%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}

.fr-grid-row {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.fr-grid-row--top {
  align-items: flex-start;
}

.fr-grid-row--bottom {
  align-items: flex-end;
}

.fr-grid-row--middle {
  align-items: center;
}

.fr-grid-row--left {
  justify-content: flex-start;
}

.fr-grid-row--right {
  justify-content: flex-end;
}

.fr-grid-row--center {
  justify-content: center;
}

.fr-col--top {
  align-self: flex-start;
}

.fr-col--bottom {
  align-self: flex-end;
}

.fr-col--middle {
  align-self: center;
}

.fr-container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}

.fr-container--fluid {
  padding-left: 0;
  padding-right: 0;
  max-width: none;
}

.fr-grid-row--gutters {
  margin: -0.5rem;
}

.fr-grid-row--gutters > [class^="fr-col-"],
.fr-grid-row--gutters > [class*=" fr-col-"],
.fr-grid-row--gutters > .fr-col {
  padding: 0.5rem;
}

.fr-grid-row--no-gutters {
  margin: 0;
}

.fr-grid-row--no-gutters > [class^="fr-col-"],
.fr-grid-row--no-gutters > [class*=" fr-col-"],
.fr-grid-row--no-gutters > .fr-col {
  padding: 0;
}

