/*
---
name: Follow
category: Components
---

# Follow button

A simple button to follow various entities on the website. On mobile, it's shown as an icon only.
Its `aria-label` changes based on the favorite status and includes the action `add to favorites` or `remove from favorites`.
See Vue file for more informations about props and usage.

```follow.html
<button
    type="button"
    class="fr-btn fr-btn--secondary follow-button"
    aria-label="63 favourites, add to favorites"
>
    <span class="magic row-inline" style="color: white;">
        <svg aria-hidden="true" width="16" height="15" viewBox="0 0 16 15" xmlns="http://www.w3.org/2000/svg">
            <path d="M7.20703 1.24224L5.42968 4.87896L1.41015 5.45318C0.699214 5.56255 0.425776 6.43755 0.945308 6.95709L3.8164 9.77349L3.13281 13.7383C3.02343 14.4493 3.78906 14.9961 4.41796 14.668L8 12.7813L11.5547 14.668C12.1836 14.9961 12.9492 14.4493 12.8398 13.7383L12.1562 9.77349L15.0273 6.95709C15.5469 6.43755 15.2734 5.56255 14.5625 5.45318L10.5703 4.87896L8.76562 1.24224C8.46484 0.613336 7.53515 0.585992 7.20703 1.24224Z" stroke="#3558A2"></path>
        </svg>
    </span>
    <span>63 favourites</span>
</button>
```
*/
.follow-button {
    .justify-center;
    min-width: 2.5rem;
    @media @query-sm {
        padding: @spacing-xs;
        .magic + span {
            display: none;
        }
    }
}
