# Embed

Embed allows you to display some GitHub-style embedding code capabilities.
This component is a single input and its accompanying copy-to-clipboard button.
See `macros/integrate.html` for common usage. The icon is `svg/copy.svg`.

```embed.html
<div class="embed-wrapper">
    <input readonly id="permalink" onClick="this.select();"
           value="https://www.data.gouv.fr/fr/datasets/demandes-de-valeurs-foncieres/" />
    <label for="permalink" class="icon-copy" id="permalink-copy"
           data-clipboard-target="#permalink" title="Copy this">
        <svg width="11" height="13" viewBox="0 0 11 13" fill="none" xmlns="http://www.w3.org/2000/svg">
            <path d="M10.6484 2.04688L9.45312 0.851562C9.26562 0.664062 8.91406 0.5 8.65625 0.5H4.625C3.99219 0.5 3.5 1.01562 3.5 1.625V2.75H1.625C0.992188 2.75 0.5 3.26562 0.5 3.875V11.375C0.5 12.0078 0.992188 12.5 1.625 12.5H6.875C7.48438 12.5 8 12.0078 8 11.375V10.25H9.875C10.4844 10.25 11 9.75781 11 9.125V2.84375C11 2.58594 10.8359 2.23438 10.6484 2.04688ZM8.75 1.27344C8.79688 1.29688 8.86719 1.32031 8.91406 1.36719L10.1328 2.58594C10.1797 2.63281 10.2031 2.70312 10.2266 2.75H8.75V1.27344ZM7.25 11.375C7.25 11.5859 7.0625 11.75 6.875 11.75H1.625C1.41406 11.75 1.25 11.5859 1.25 11.375V3.875C1.25 3.6875 1.41406 3.5 1.625 3.5H3.5V9.125C3.5 9.75781 3.99219 10.25 4.625 10.25H7.25V11.375ZM10.25 9.125C10.25 9.33594 10.0625 9.5 9.875 9.5H4.625C4.41406 9.5 4.25 9.33594 4.25 9.125V1.625C4.25 1.4375 4.41406 1.25 4.625 1.25H8V2.9375C8 3.26562 8.23438 3.5 8.5625 3.5H10.25V9.125Z" fill="#232323"/>
        </svg>
    </label>
</div>
