.embed-wrapper {
    display: flex;
    border: 1px solid @grey-200;
    border-radius: 4px;
    background: @grey-50;

    input, textarea {
        flex-grow: 1;
        appearance: none;
        border: none;
        background: none;
        padding: @spacing-xs;
        color: @grey-380;

        font-size: @font-size-sm;
        line-height: @spacing-lg;

        white-space: nowrap;
        resize: none;
    }

    .icon-copy {
        svg {
            width: 24px;
            height: 24px;

            * {
                fill: @grey-380;
            }
        }

        border-left: 1px solid @grey-200;
        padding: @spacing-xs;
        cursor: pointer;
    }
}
